.NaviBar {
  position: fixed;
  display: flex;
  justify-content: center;
  left: 0px;
  top: 0px;
  height: 100%;
  width: 250px;
  border: 1em solid black;
  z-index: 1;
}

.NaviBar li {
  padding-right: 1.5em;
  margin-top: 2em;
  font-size: large;
  font-weight: 800;
  list-style-type: none;
}