.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 0;
}

.Header {
  background-color: #282c34;
  position: fixed;
  width: 100%;
  min-height: 100vh;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  border: 1em solid black;
}

.Body {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 1090px;
  margin-top: 100px;
  margin-left: 250px;
  border: 1em solid black;
}

.Block {
  border: medium solid black;
  border-radius: 2em;
}